<template>
    <div class="dashboard-container">
        <el-container>
            <el-header class="header">
                <div class="logo">MPC</div>
                <div class="user-info">
                    <span>, {{ user.username }} ({{ roleText }})</span>
                    <el-button type="text" @click="handleLogout"></el-button>
                </div>
            </el-header>

            <el-container>
                <el-aside width="250px">
                    <el-menu :default-active="activeMenu" router class="menu" background-color="#304156"
                        text-color="#bfcbd9" active-text-color="#409EFF">
                        <el-menu-item v-if="isAdmin" index="/users">
                            <i class="el-icon-user"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item v-if="isAdmin || isCoordinator" index="/keygen">
                            <i class="el-icon-key"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item v-if="isAdmin || isCoordinator" index="/sign">
                            <i class="el-icon-edit"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item index="/notifications">
                            <i class="el-icon-bell"></i>
                            <span></span>
                            <el-badge v-if="notifications.length > 0" :value="notifications.length" class="item">
                            </el-badge>
                        </el-menu-item>
                    </el-menu>
                </el-aside>

                <el-main>
                    <router-view></router-view>

                    <div v-if="$route.path === '/dashboard'" class="welcome-container">
                        <div class="welcome-message">
                            <i class="el-icon-s-home welcome-icon"></i>
                            <h2></h2>
                            <p></p>

                            <div v-if="isAdmin" class="feature-box">
                                <h3></h3>
                                <el-button type="primary" @click="$router.push('/users')"></el-button>
                                <el-button type="primary" @click="$router.push('/keygen')"></el-button>
                                <el-button type="primary" @click="$router.push('/sign')"></el-button>
                            </div>

                            <div v-else-if="isCoordinator" class="feature-box">
                                <h3></h3>
                                <el-button type="primary" @click="$router.push('/keygen')"></el-button>
                                <el-button type="primary" @click="$router.push('/sign')"></el-button>
                            </div>

                            <div v-else-if="isParticipant" class="feature-box">
                                <h3></h3>
                                <p></p>
                                <el-button type="primary" @click="$router.push('/notifications')"></el-button>
                            </div>
                        </div>
                    </div>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { initWebSocketService, resetWebSocketService } from '../services/ws'

export default {
    name: 'Dashboard',
    data() {
        return {
            activeMenu: this.$route.path,
            wsCheckInterval: null,
            wsErrorCount: 0,
            lastWsReset: 0,
            healthCheckInterval: null
        }
    },
    computed: {
        ...mapGetters([
            'currentUser',
            'isAdmin',
            'isCoordinator',
            'isParticipant',
            'notifications',
            'wsConnected'
        ]),
        user() {
            return this.currentUser || {}
        },
        roleText() {
            if (this.isAdmin) return ''
            if (this.isCoordinator) return ''
            if (this.isParticipant) return ''
            return ''
        },
        // 
        pendingInvitations() {
            return this.notifications.filter(n =>
                (n.type === 'keygen_invite' || n.type === 'sign_invite') &&
                !n.responded
            )
        },
        // 
        hasUnhandledNotifications() {
            return this.pendingInvitations.length > 0
        }
    },
    created() {
        // WebSocket
        this.ensureWebSocketConnection()

        // WebSocket
        this.wsCheckInterval = setInterval(() => {
            this.checkWsConnection()
        }, 10000) // 10

        // 
        this.checkPendingNotifications()
    },
    mounted() {
        // 
        if (this.isParticipant) {
            this.notificationCheckInterval = setInterval(() => {
                this.checkPendingNotifications()
            }, 60000) // 
        }

        // 
        document.addEventListener('visibilitychange', this.handleVisibilityChange)
        
        // 
        this.healthCheckInterval = setInterval(() => {
            this.checkWsConnection()
        }, 60000) // 
    },
    beforeDestroy() {
        // 
        if (this.wsCheckInterval) {
            clearInterval(this.wsCheckInterval)
        }
        if (this.notificationCheckInterval) {
            clearInterval(this.notificationCheckInterval)
        }

        // 
        document.removeEventListener('visibilitychange', this.handleVisibilityChange)
        
        // 
        if (this.healthCheckInterval) {
            clearInterval(this.healthCheckInterval)
        }
    },
    methods: {
        // 
        handleVisibilityChange() {
            if (!document.hidden) {
                // WebSocket
                console.log('WebSocket')
                this.checkWsConnection()
            }
        },

        // WebSocket
        checkWsConnection() {
            // store
            this.$store.dispatch('checkWebSocketHealth')
        },

        // WebSocket
        resetWsConnection() {
            // 
            import('../services/ws').then(({ resetWebSocketService }) => {
                resetWebSocketService()
                this.$store.dispatch('resetWebSocketConnection')
            })
        },

        // WebSocket
        ensureWebSocketConnection() {
            this.$store.dispatch('connectWebSocket')

            // WebSocket
            setTimeout(() => {
                initWebSocketService()
            }, 1000)  // 1
        },

        // 
        checkPendingNotifications() {
            if (this.hasUnhandledNotifications && this.$route.path !== '/notifications') {
                this.$notify({
                    title: '',
                    message: ` ${this.pendingInvitations.length} `,
                    type: 'warning',
                    duration: 5000
                })

                // 3
                setTimeout(() => {
                    this.$router.push('/notifications')
                }, 3000)
            }
        },

        // 
        handleLogout() {
            this.$store.dispatch('logout')
            this.$router.push('/login')
        }
    },
    watch: {
        // 
        '$route.path'(newPath) {
            this.activeMenu = newPath
        },

        // WebSocket
        wsConnected(connected) {
            if (connected) {
                console.log('WebSocket')
                this.wsErrorCount = 0  // 
            } else {
                console.warn('WebSocket')
            }
        },

        // 
        notifications(newNotifications) {
            // 
            if (this.hasUnhandledNotifications && this.$route.path !== '/notifications') {
                this.checkPendingNotifications()
            }
        }
    }
}
</script>

