// 
async function handleSignParams(message) {
    try {
        // MPC
        const signResponse = await mpcApi.sign({
            parties: message.parties,
            data: message.data,
            filename: message.filename,
            encryptedKey: message.encrypted_shard,
            userName: store.state.user.username,
            address: message.address,
            signature: message.signature
        })

        if (signResponse.data.success) {
            // 
            sendWSMessage({
                type: WS_MESSAGE_TYPES.SIGN_RESULT,
                session_key: message.session_key,
                part_index: message.part_index,
                success: true,
                signature: signResponse.data.signature,
                message: ''
            })

            Message.success('')
        } else {
            throw new Error('MPC')
        }
    } catch (error) {
        console.error(':', error)
        // 
        sendWSMessage({
            type: WS_MESSAGE_TYPES.SIGN_RESULT,
            session_key: message.session_key,
            part_index: message.part_index,
            success: false,
            signature: '',
            message: ': ' + error.message
        })

        Message.error(': ' + error.message)
    }
}

// 
function handleSignComplete(message) {
    if (message.success) {
        MessageBox.alert(
            `! : ${message.signature}`,
            '',
            { type: 'success' }
        )
    } else {
        MessageBox.alert(
            `: ${message.message}`,
            '',
            { type: 'error' }
        )
    }
}

// 
function handleError(message) {
    console.error(':', message)
    Message.error(`: ${message.message}`)
} 