<template>
    <div class="users-container">
        <el-card>
            <div slot="header">
                <span></span>
            </div>

            <el-table :data="userList" v-loading="loading" style="width: 100%">
                <el-table-column prop="username" label="" width="180"></el-table-column>
                <el-table-column prop="email" label="" width="220"></el-table-column>
                <el-table-column prop="role" label="" width="120"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.newRole" placeholder="" size="small">
                            <el-option label="" value="admin"></el-option>
                            <el-option label="" value="coordinator"></el-option>
                            <el-option label="" value="participant"></el-option>
                            <el-option label="" value="guest"></el-option>
                        </el-select>
                        <el-button type="primary" size="small" :disabled="scope.row.role === scope.row.newRole"
                            @click="updateUserRole(scope.row)" style="margin-left: 10px">
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <div v-if="userList.length === 0 && !loading" class="empty-state">
                <p></p>
                <el-button type="primary" size="small" @click="fetchUserList"></el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
import { userApi } from '../services/api'

export default {
    name: 'Users',
    data() {
        return {
            userList: [],
            loading: false
        }
    },
    created() {
        this.fetchUserList()
    },
    methods: {
        async fetchUserList() {
            this.loading = true
            try {
                const response = await userApi.getUsers()
                // API
                const users = response.data.users || response.data.data || []
                this.userList = users.map(user => ({
                    ...user,
                    newRole: user.role
                }))

                if (this.userList.length === 0) {
                    this.$message.warning('')
                }
            } catch (error) {
                console.error(':', error)
                this.$message.error(': ' + (error.response?.data?.error || error.message))
            } finally {
                this.loading = false
            }
        },

        async updateUserRole(user) {
            try {
                await userApi.updateUserRole(user.username, user.newRole)
                this.$message.success(` ${user.username}  ${user.newRole}`)
                // 
                user.role = user.newRole
            } catch (error) {
                console.error(':', error)
                this.$message.error('')
            }
        }
    }
}
</script>

<style scoped>
.users-container {
    padding: 20px;
}

.empty-state {
    text-align: center;
    padding: 40px 0;
    color: #606266;
}
</style>