<template>
    <div class="login-container">
        <el-card class="login-card">
            <div slot="header" class="card-header">
                <h2></h2>
            </div>

            <el-form :model="loginForm" :rules="rules" ref="loginForm" label-width="0px">
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="el-icon-user" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" placeholder="" show-password>
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" :loading="loading" @click="handleLogin" style="width: 100%">
                        
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <div class="register-link">
                        <span>?</span>
                        <router-link to="/register"></router-link>
                    </div>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { userApi } from '../services/api'

export default {
    name: 'Login',
    data() {
        return {
            loginForm: {
                username: '',
                password: ''
            },
            rules: {
                username: [{ required: true, message: '', trigger: 'blur' }],
                password: [{ required: true, message: '', trigger: 'blur' }]
            },
            loading: false
        }
    },
    methods: {
        handleLogin() {
            this.$refs.loginForm.validate(async valid => {
                if (!valid) {
                    return false
                }

                this.loading = true

                try {
                    const response = await userApi.login({
                        username: this.loginForm.username,
                        password: this.loginForm.password
                    })

                    // 
                    if (!response.data || !response.data.token) {
                        throw new Error('')
                    }

                    // token
                    const token = response.data.token.startsWith('Bearer ') 
                        ? response.data.token 
                        : `Bearer ${response.data.token}`
                    
                    // token
                    const userData = {
                        token: token,
                        user: response.data.user
                    }

                    // 
                    this.$store.dispatch('login', userData)

                    // 
                    console.log('', userData.user.username, userData.user.role)

                    // WebSocket
                    this.$store.dispatch('connectWebSocket')

                    // WebSocket
                    setTimeout(() => {
                        // 
                        this.$router.push('/dashboard')

                        this.$message.success('')
                    }, 500)
                } catch (error) {
                    console.error(':', error)
                    let errorMsg = ''
                    
                    if (error.response) {
                        errorMsg = error.response.data?.error || error.response.data?.message || errorMsg
                    } else if (error.message) {
                        errorMsg = error.message
                    }
                    
                    this.$message.error(errorMsg)
                } finally {
                    this.loading = false
                }
            })
        }
    }
}
</script>

